	module.exports = {"pathContext":{"frontmatter":{"path":"docs/flex-direction","title":"Flex Direction","hasPlayground":true,"initialPlayground":null,"redirect":null},"html":"<h2>Flex Direction</h2>\n<p>Flex direction controls the direction in which children of a node are laid out.\nThis is also referred to as the main axis. The main axis is the direction in\nwhich children are laid out. The cross axis the the axis perpendicular to the\nmain axis, or the axis which wrapping lines are laid out in.</p>\n<p><strong>ROW (DEFAULT)</strong> Align children from left to right. If <a href=\"flex-wrap\">wrapping</a> is enabled then\nthe next line will start under the first item on the left of the container.</p>\n<p><strong>COLUMN</strong> Align children from top to bottom. If <a href=\"flex-wrap\">wrapping</a> is enabled then\nthe next line will start to the left first item on the top of the container.</p>\n<p><strong>ROW REVERSE</strong> Align children from right to left. If <a href=\"flex-wrap\">wrapping</a> is enabled then\nthe next line will start under the first item on the right of the container.</p>\n<p><strong>COLUMN REVERSE</strong> Align children from bottom to top. If <a href=\"flex-wrap\">wrapping</a> is enabled then\nthe next line will start to the left first item on the bottom of the container.</p>\n<p><controls prop=\"flexDirection\"></controls></p>"}}

